:root{
  --font-dark1 : #262626;
  --font-dark2 : #333333;
  --font-dark-common : #2c3e50;
  --font-dark-remark : #909399;
  --tint-gray : #d9d9d9;
  --border1 : 1px solid #d9d9d9;
  --border2 : 1px solid #eaecef;
  --box-shadow1 : 1px 1px 8px #d9d9d9,0 0 7px #d9d9d9;
  --box-shadow2 : 0 2px 10px 0 rgba(0, 0, 0, 0.15);
  --green1 : #5ad8a6;
  --green2 : #3eaf7c; 
  --origin : #ff9845;
  --gray : #5d7092;
  --blue : #5b8ff9;
  --hover-speed : .4s ease-out;
  --transition-speed : .8s;
}
::-webkit-scrollbar-thumb:hover{
 background-color: var(--green1);
}
*{
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  max-width: 100%;
  font-family: Ubuntu,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;
  line-height: 1.5;
  scroll-behavior:smooth;
  font-weight: 400;
  letter-spacing: 1px;
	position: relative;
}
body{
	color: var(--font-dark-common);
}
h1{
  font-size: 2em;
  font-weight: 500;
  letter-spacing: 2px;
}
h2{
  font-size: 1.6em;
  font-weight: 500;
  letter-spacing: 2px;
}
h3{
 font-size: 1.3em; 
 font-weight: 500;
}
input,textarea{
  outline: none;
  border-radius: 5px;
  font-size: 1em;
  vertical-align: middle;
  border: var(--border1);
}
input::-webkit-input-placeholder,textarea::-webkit-input-placeholder{
  color: var(--font-dark-remark);
  font-size: 0.9em;
}
input:focus,textarea:focus{
  border-color: var(--green1);
  -webkit-box-shadow: 0 0 2px var(--green1);
          box-shadow: 0 0 2px var(--green1);
}
a{
  text-decoration: none;
}
a,i,button{
  -webkit-tap-highlight-color: rgba(255, 0, 0, 0);
}
.btn{
	padding: 0 10px;
	border: none;
	background-color: var(--green1);
	color: #FFFFFF;
	border-radius: 5px;
	cursor: pointer;
	outline: none;
	text-align: center;
}
.btn:hover{
	-webkit-box-shadow: 0 0 5px var(--green2);
	        box-shadow: 0 0 5px var(--green2);
}
@media  (max-width:720px){
